

export default {
	getTabClass (tab, good, top) {
		if(top) return 'top';
		if(good) return 'good';
		return tab || 'all';
	},
	getTabName (tab, good, top) {
		if(top) return '';
		if(good) return '';
		if(tab === 'share') return '';
		if(tab === 'ask') return '';
		if(tab === 'job') return '';
		return '';
	}
}


